body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

@font-face {
  font-family: 'Rubik';
  src: url(/static/media/Rubik.34239b27.woff2) format('woff2'),
  url(/static/media/Rubik.d0ded419.woff) format('woff'),
  url(/static/media/Rubik.017cb38c.ttf) format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Rubik';
  src: url(/static/media/Rubikbold.55c75b79.woff2) format('woff2'),
  url(/static/media/Rubikbold.88e2aa7c.woff) format('woff'),
  url(/static/media/Rubikbold.20f49859.ttf) format('truetype');
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: 'Rubik';
  src: url(/static/media/RubikBold800.df74017e.woff2) format('woff2'),
  url(/static/media/RubikBold800.381a3cfd.woff) format('woff'),
  url(/static/media/RubikBold800.2ab53458.ttf) format('truetype');
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: 'Raleway';
  src: url(/static/media/ralewayextrabold.b1127e00.woff2) format('woff2'),
  url(/static/media/ralewayextrabold.34e74a2f.woff) format('woff'),
  url(/static/media/ralewayextrabold.ae8bbc9c.ttf) format('truetype');
  font-weight: 800;
  font-style: normal;
}

:root {
  --size-all: 18px;
  --size-validate: 14px;
  --size-text: 16px;
  --color-black: #000000;
  --color-dark: #444444;
  --color-white: #FFFFFF;
  --color-gray: #DCDCDC;
  --color-gray-light: #EBEBEB;
  --color-yellow-light: #FFE17F;
  --color-yellow-dark: #FFC700;
  --color-red-error:#FF3D3D;
  --color-purple: #AF4FFF;
  --color-light: #F0F0F0;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Rubik', sans-serif;
  font-size: 18px;
  font-size: var(--size-all);
}

.list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.link {
  display: inline-block;
  position: relative;
  color: #000000;
  color: var(--color-black);
  text-decoration: none;
  outline: none;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 30px;
}
.header {
    position: relative;
    padding-top: 40px;
}

.header__container {
    width: 100%;
    padding: 49px 60px 0 60px;
    background-color: var(--color-yellow-light);
    height: 300px;
}

.header__logo {
    position: absolute;
    top: 17px;
    outline: none;
}

.menu__user {
    display: flex;
    margin-bottom: 44px;
    grid-gap: 24px;
    gap: 24px;
}

.menu__form-search {
    display: flex;
    position: relative;
    align-items: center;
    max-width: 516px;
    width: 100%;
}

.menu__search-label {
    z-index: 10;
    position: absolute;
    content: '';
    width: 16px;
    height: 16px;
    top: 15%;
    background: url(/static/media/search.b0eeeaee.svg) no-repeat;
    background-size: cover;
}

.menu__search-field {
    z-index: 1;
    width: 100%;
    padding: 0 36px;
    font-family: 'Rubik', sans-serif;
    font-size: var(--size-text);
    background: none;
    border: none;
    outline: none;
}

.menu__search-field::-webkit-input-placeholder {
    font-size: var(--size-text);
    opacity: 0.3;
}

.menu__search-field:-ms-input-placeholder {
    font-size: var(--size-text);
    opacity: 0.3;
}

.menu__search-field::placeholder {
    font-size: var(--size-text);
    opacity: 0.3;
}

.menu__search-field:hover.menu__search-field::-webkit-input-placeholder {
    opacity: 0.6;
}

.menu__search-field:hover.menu__search-field:-ms-input-placeholder {
    opacity: 0.6;
}

.menu__search-field:hover.menu__search-field::placeholder {
    opacity: 0.6;
}

.menu__search_custom {
    display: none;
    position: absolute;
    top: -42%;
    left: -1%;
    width: 101%;
    height: 40px;
    border-radius: 20px;
    text-replace: all .3s;
}

.menu__search-field:focus-visible + .menu__search_custom {
    display: block;
    border: 2px solid var(--color-purple);
}

.menu__search-field_active:focus-visible + .menu__search_custom {
    display: block;
    border: 2px solid var(--color-black);
}

.menu__search-btn {
    display: none;
    z-index: 1;
    position: relative;
    max-width: 160px;
    width: 100%;
    right: 0;
    font-family: 'Rubik', sans-serif;
    background: none;
    border: none;
    cursor: pointer;
    outline: none;
    transition: .5s;
}

.menu__search-btn_active {
    display: block;
}

.menu__search-btn::after {
    position: absolute;
    content: '';
    z-index: -1;
    top: -60%;
    left: -2.5%;
    width: 100%;
    height: 36px;
    border: 2px solid var(--color-black);
    border-bottom-right-radius: 20px;
    border-top-right-radius: 20px;
    transition: .5s;
}

.menu__search-btn:focus-visible::after {
    border-color: var(--color-purple);
    transition: .2s;
}

.menu__search-btn:hover {
    color: var(--color-white);
    transition: .5s;
}

.menu__search-btn:active {
    color: rgba(255, 255, 255, .3);
    transition: .5s;
}

.menu__search-btn:hover::after {
    background-color: var(--color-black);
    transition: .5s;
}

.menu__user-list {
    display: flex;
    align-items: center;
    width: 100%;
    grid-gap: 15px;
    gap: 15px;
    font-size: var(--size-text);
}

.menu__user-link {
    transition: .3s;
}

.menu__user-link:hover {
    opacity: 0.6;
    transition: .3s;
}

.menu__user-link:active {
    opacity: 0.3;
    transition: .3s;
}

.menu__user-link::after {
    position: absolute;
    content: '';
    top: -50%;
    height: 36px;
    border-radius: 20px;
    transition: border .3s;
}

.menu__user-link:focus-visible::after {
    border: 2px solid var(--color-purple);
    transition: border .3s;
}

.user-enter {
    max-width: 245px;
    width: 100%;
}

.enter {
    padding-left: 25px;
    background: url(/static/media/enter.3288be39.svg) no-repeat 0 center;
}

.enter:focus-visible::after {
    left: -30%;
    width: 117px;
}

.user-compare {
    width: 120px;
}

.compare {
    padding-left: 20px;
    background: url(/static/media/compare.7a904c90.svg) no-repeat 0 center;
}

.compare:focus-visible::after {
    left: -25%;
    width: 136px;
}

.basket {
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    background: url(/static/media/basket.a35ea1ec.svg) no-repeat 15px 20%;
}

.basket:focus-visible::after {
    left: 0;
    width: 131px;
}

.user-basket__count {
    display: flex;
    width: 21px;
    height: 21px;
    align-items: center;
    justify-content: center;
    font-size: var(--size-validate);
    background-color: var(--color-white);
    border-radius: 100%;
}

/*Основное меню*/

.menu__nav {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.menu__nav-catalog {
    width: 50%;
}

.menu__link-catalog {
    z-index: 1;
    padding-right: 67px;
    font-family: 'Raleway', sans-serif;
    transition: opacity .3s;
}

.menu__link-catalog::after {
    position: absolute;
    content: '';
    top: -50%;
    left: -2%;
    width: 80%;
    height: 36px;
    border-radius: 20px;
    transition: border .3s;
}

.menu__link-catalog:focus-visible::after {
    border: 2px solid var(--color-purple);
    transition: border .3s;
}

.menu__link-custom {
    position: absolute;
    width: 50px;
    height: 50px;
    top: -70%;
    right: 0;
    border: 2px solid var(--color-black);
    border-radius: 100%;
    opacity: 0.2;
    transition: opacity .3s;
}

.menu__link-custom::before {
    content: '';
    position: absolute;
    top: 46%;
    right: 32%;
    width: 16px;
    height: 2px;
    background-color: var(--color-black);
}

.menu__link-custom::after {
    content: '';
    position: absolute;
    top: 46%;
    right: 32%;
    width: 16px;
    height: 2px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    background-color: var(--color-black);
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

.menu__nav-block {
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.menu__nav-block:hover .menu__link-custom::after {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

.menu__link-catalog:focus-visible + .menu__catalog {
    display: flex;
    transition: .3s;
}

.menu__nav-block:hover .menu__catalog {
    display: flex;
    transition: .3s;
}


.menu__nav-block:hover .menu__link-custom {
    opacity: 1;
    transition: opacity .3s;
}

.menu__link-catalog:active {
    opacity: 0.3;
    transition: opacity .3s;
}

.menu__nav-list {
    display: flex;
    max-width: 500px;
    width: 100%;
    grid-gap: 19px;
    gap: 19px;
    font-family: 'Raleway', sans-serif;
}

.menu__nav-item {
    position: relative;
}

.menu__link {
    transition: all .3s;
}

.menu__link::before {
    position: absolute;
    content: '';
    top: -50%;
    left: -8%;
    width: 110%;
    height: 36px;
    border-radius: 20px;
    border: none;
    transition: border .3s;
}

.menu__link:focus-visible::before {
    border: 2px solid var(--color-purple);
    transition: border .3s;
}

.menu__link::after {
    position: absolute;
    content: '';
    bottom: -4px;
    left: 0;
    width: 0;
    height: 2px;
    background-color: var(--color-black);
    transition: width .5s;
}

.menu__link:hover::after {
    width: 97%;
}

.menu__link:active {
    opacity: .3;
}

.menu__link_spacing {
    letter-spacing: 0.205em;
    margin-right: -0.205em;
}

.menu__catalog {
    z-index: 10;
    position: absolute;
    display: none;
    max-width: 1160px;
    top: 163%;
    height: 180px;
    width: 100vw;
    margin-left: -60px;
    padding-left: 60px;
    padding-top: 33px;
    grid-gap: 29px;
    gap: 29px;
    font-family: 'Rubik', sans-serif;
    font-size: var(--size-text);
    background-color: var(--color-yellow-light);
    transition: height .3s;
}

.menu__catalog-item__list {
    display: flex;
    flex-direction: column;
    padding-right: 67px;
    grid-gap: 18px;
    gap: 18px;
}

.menu__catalog-link {
    padding: 0  ;
    font-family: 'Rubik', sans-serif;
}

.menu__catalog-link::after {
    width: 110%;
    left: -6%;
}

.menu__nav-delivery,
.menu__nav-guarantee {
    width: 140px;
}

.menu__nav-contacts {
    width: 181px;
    text-align: right;
}
.section__title {
    margin: 0;
    font-family: 'Raleway', sans-serif;
    font-size: 50px;
    line-height: 1;
}
.hero {
    position: relative;
    height: 480px;
}

.title-opacity {
    position: absolute;
    top: 0;
    opacity: 0;
}

.hero__slide {
    display: none;
    grid-gap: 40px;
    gap: 40px;
    position: relative;
    max-width: 1160px;
    width: 100%;
    top: -150px;
    transition: opacity ease-in-out 0.4s;
}

.active-slide {
    display: flex;
}

.hero__slide__number {
    position: absolute;
    display: block;
    top: 10px;
    right: 49px;
    color: var(--color-white);
    font-size: 180px;
    line-height: 1;
    font-weight: 700;
}

.hero__text {
    z-index: 2;
    padding-top: 121px;
}

.hero__title {
    margin-bottom: 30px;
}

.hero__description {
    margin-bottom: 50px;
    line-height: 30px;
}

.hero__link {
    display: flex;
    align-items: center;
    margin-bottom: 70px;
    width: 220px;
    justify-content: center;
    font-family: 'Raleway', sans-serif;
    letter-spacing: .2em;
    transition: all .3s;
}

.hero__link::after {
    z-index: -1;
    position: absolute;
    content: '';
    width: 100%;
    height: 8px;
    background-color: var(--color-yellow-light);
    transition: all .3s;
}

.hero__link:focus-visible::after {
    background-color: var(--color-purple);
}

.hero__link:hover::after {
    height: 50px;
}

.hero__link:active {
    color: rgba(0, 0, 0, .3);
}

.hero__params {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.hero__params__param {
    margin-top: 0;
    margin-bottom: 14px;
    font-size: 36px;
}

.hero__params__name {
    margin: 0;
    font-size: var(--size-text);
    color: var(--color-dark);
}


.hero__dots {
    z-index: 10;
    position: absolute;
    display: flex;
    bottom: 253px;
    right: 60px;
    grid-gap: 22px;
    gap: 22px;
}

.hero__dot {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 12px;
    height: 12px;
    padding: 0;
    border-radius: 100%;
    border: 2px solid var(--color-black);
    background: none;
    cursor: pointer;
    transition: all .3s;
    outline: none;
}

.hero__dot:focus-visible {
    border-color: var(--color-purple);
}

.hero__dot:hover {
    opacity: .6;
}

.hero__dot:active {
    opacity: .3;
}

.hero__dot_active {
    background-color: var(--color-black);
    cursor: auto;
}

.hero__dot_active:focus-visible,
.hero__dot_active:hover,
.hero__dot_active:active {
    border: 2px solid var(--color-black);
    opacity: 1;
}



.slider__btn {
    z-index: 10;
    position: absolute;
    bottom: 320px;
    width: 40px;
    height: 80px;
    border: none;
    border-radius: 6px;
    background: none;
    cursor: pointer;
    transition: all .3s;
}

.slider__btn_prev {
    left: 49px;
    background: url(/static/media/left-tall-arrow.c55f7286.svg) no-repeat center;
}

.slider__btn_next {
    left: 551px;
    background: url(/static/media/right-tall-arrow.548bf85e.svg) no-repeat center;
}

.slider__btn:focus-visible {
    outline: 2px solid var(--color-purple);
}

.slider__btn:hover {
    opacity: .6;
}

.slider__btn:active {
    opacity: .3;
}

.category {
    position: relative;
    margin-bottom: 69px;
}

.category__list {
